<template>
  <div class="container">
    <div id="text-box"><div style="margin: auto">{{data[activeIndex].text}}</div></div>

    <div id="item-bar">
      <div v-for="(item,index) in data" :key="index"
           :class="{selectedBtn:index==activeIndex}" @click="changeBtn(index)">{{index+1}}</div>
    </div>
    <div id="tip-bar">
      <p v-if="recording">正在录音...</p>
      <p v-else>长按按钮进行录音</p>
    </div>

    <div id="audio-bar">
      <div id="record" :class="{btnMd:recording}" @mousedown="startRecording" @mouseup="stopRecording"
           @touchstart="startRecording" @touchend="stopRecording"><i class="el-icon-microphone"></i></div>
      <div id="play" v-if="data[activeIndex].audio"><i class="el-icon-headset" @click="playAudio"></i></div>
    </div>
    <el-button id="upload" @click="submit" v-if="data[data.length-1].audio" type="primary">提交数据</el-button>

    <audio style="visibility: hidden" ref="audioPlayer"></audio>
  </div>
</template>

<script>
  import {v1 as uuid} from 'uuid';
  import Recorderx, { ENCODE_TYPE } from "recorderx"
  import api from '../api'

  export default {
    name: 'Annotator',
    data(){
      return {
        client:null,
        data:[],

        activeIndex:0,

        recording:false,

        recorder:null,
        audio:null,
      }
    },
    methods:{
      changeBtn(index){

        if(this.data[this.activeIndex].audio && index == this.activeIndex+1)
          this.activeIndex=index
        else if (index < this.activeIndex)
          this.activeIndex=index
        else if (index > this.activeIndex && this.data[index-1].audio){

          console.log(index)
          this.activeIndex=index
        }
      },

      setUid(){
        var cookies = document.cookie
        if(cookies.indexOf('client') == -1){
          var uid = uuid().split("-").join("")
          document.cookie=`client=${uid}; expires=Thu, 18 Dec 2043 12:00:00 GMT`
        }

        this.client = cookies.slice(7)

      },
      getTextList(){
        api.getTextList().then(({data:{data,code}})=>{
          if(code==200)
            this.data = data
            this.activeIndex = 0
        })
      },
      submit(){
        for (let i=0;i<this.data.length;++i){
          if (!this.data[i].audio){
            var msg = `请检查第${i}条录音!`
            this.$message.error(msg)
            return 0
          }
        }

        for (let i=0;i<this.data.length;++i){
          var form = new FormData()

          form.append('data',this.data[i].audio,
                  `${this.data[i].id}-${this.data[i].text}-${this.client}.wav`)

          api.uploadAudios(form).then(({data:{code}})=>{
            if(code==200 && i==this.data.length-1){
              this.$message.success('本轮提交成功!')
              this.getTextList()
            }
          }).catch((e)=>{
            this.$message.err(e)
            return 0
          })
        }

      },
      startRecording() {
        this.recording = true

        this.recorder.start()
                .then(() => {
                })
                .catch(error => {
                  console.log("Recording failed.", error);
                });

      },
      stopRecording(){

        setTimeout(()=>{
          this.recording = false

          this.recorder.pause();

          var blob = this.recorder.getRecord({
            encodeTo: ENCODE_TYPE.WAV,
            compressible: false
          });

          this.pushData(blob)

          this.recorder.clear()

          // if (this.activeIndex<this.data.length+1)
          //   this.activeIndex += 1

        },700)


      },
      pushData(blob){
        this.data[this.activeIndex].audio = blob
      },

      playAudio(){
        var el = this.$refs.audioPlayer
        var link = URL.createObjectURL(this.data[this.activeIndex].audio)
        el.src = link

        el.currentTime = 0
        el.play()
      }
    },
    created() {
      this.recorder = new Recorderx()

      this.setUid()

      this.getTextList()
    }
  }
</script>

