import axios from 'axios';

const instance = axios.create({
    baseURL:'https://audio.6180339.club/'
    // baseURL:'http://127.0.0.1:5000/'
});


export default {

    getTextList(){
        return instance.post('api/text/textList')
    },
    uploadAudios(data){
        return instance.post('api/audio/uploadAudio',data,)
    }
}